<template>
  <div class="page">
    <div class="page__content">
      <div class="page__header">
        <h1 class="page__title">
          Новое событие
        </h1>
      </div>

      <div class="create-event">
        <div class="create-event__form">
          <v-input
            :label="'Заголовок'"
            :error="$v.form.title.$error"
            v-model="$v.form.title.$model"
            :text-error="'введите заголовок'"
            require-input
          />
          <v-select
            :label="'Стартап'"
            :options="STARTUP"
            :value="form.startup"
            @select-change="form.startup = $event"
            :show-error="$v.form.startup.$error"
            :error-text="'укажите стартап'"
            require-input
          >
          </v-select>
          <v-select
            :label="'Площадка проведения'"
            :options="PLACE"
            :value="form.place"
            @select-change="form.place = $event"
            :error-text="'укажите необходимую площадку проведения'"
          >
          </v-select>
          <v-multiselect
            :label="'Категория'"
            :options="CATEGORY"
            :value="form.categories"
            @multi-change="form.categories = $event"
          ></v-multiselect>
          <v-multiselect
            :label="'Пригласить резидентов'"
            :label-field="'full_name'"
            :options="members"
            :value="form.members"
            @multi-change="form.members = $event"
          ></v-multiselect>
          <div class="form-control">
            <date-picker
              v-model="form.begin_time"
              format="DD MMM YYYY HH:mm"
              type="datetime"
              value-type="timestamp"
              :disabled-date="notBeforeToday"
              :editable="false"
            >
              <i slot="icon-calendar"> </i>
            </date-picker>
            <label>Время начала события</label>
          </div>

          <div
            class="form-control"
            :class="{ 'form-control--error': $v.form.body.$error }"
          >
            <vue-editor
              v-model="$v.form.body.$model"
              :editorToolbar="customToolbar"
            ></vue-editor>
            <label v-show="!$v.form.body.$error">
              Описание события
              <span class="form-control__require">*</span>
            </label>
            <label v-show="$v.form.body.$error">введите описание события</label>
          </div>

          <file-pond
            name="test"
            ref="pond"
            label-idle="Добавить фото"
            :allow-multiple="true"
            accepted-file-types="image/jpeg, image/png"
            imagePreviewHeight="100"
            :allow-reorder="true"
            @updatefiles="handleFilePond"
            @reorderfiles="handleFilePond"
            :max-files="10"
          />

          <div class="create-event__form-group-btn">
            <button class="button-primary" @click="createEvent">
              Отправить на проверку
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import qs from "qs";
import vueFilePond from "vue-filepond";
import "filepond/dist/filepond.min.css";
import "filepond-plugin-image-preview/dist/filepond-plugin-image-preview.min.css";
import FilePondPluginFileValidateType from "filepond-plugin-file-validate-type";
import FilePondPluginImagePreview from "filepond-plugin-image-preview";
import axios from "axios";
import { VueEditor } from "vue2-editor";
import VMultiselect from "../components/vMultiselect";
import VInput from "../components/vInput";
import { mapActions, mapGetters } from "vuex";
import VSelect from "../components/vSelect";
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/locale/ru";

// Create component
const FilePond = vueFilePond(
  FilePondPluginFileValidateType,
  FilePondPluginImagePreview
);

export default {
  name: "CreateEvent",
  components: {
    VSelect,
    VInput,
    VMultiselect,
    VueEditor,
    FilePond,
    DatePicker
  },
  data() {
    return {
      form: {
        title: "",
        body: "",
        categories: [],
        place: {},
        startup: {},
        members: [],
        title_picture: {},
        images: [],
        begin_time: ""
      },
      members: [],
      title_picture: null,
      images: null,
      category: [],
      customToolbar: [
        ["bold", "italic", "underline"],
        [{ list: "ordered" }, { list: "bullet" }]
      ]
    };
  },
  validations: {
    form: {
      title: {
        required
      },
      body: {
        required
      },
      startup: {
        required
      }
    }
  },
  mounted() {
    this.GET_CATEGORY();
    this.GET_PLACE();
    this.GET_STARTUP();
    this.RESIDENTS_LIST().then(response => {
      this.members = response.data._items;
    });
  },
  methods: {
    ...mapActions("category", ["GET_CATEGORY"]),
    ...mapActions("place", ["GET_PLACE"]),
    ...mapActions("startup", ["GET_STARTUP"]),
    ...mapActions("user", ["RESIDENTS_LIST"]),
    handleFilePond: function() {
      let fileImages = this.$refs.pond.getFiles();
      this.title_picture = fileImages.map(item => item.file)[0];
      this.images = fileImages.map(item => item.file).slice(1);
    },
    async uploadImages() {
      let formDataImages = new FormData();

      this.images.forEach(item => {
        formDataImages.append("file[]", item);
      });

      await axios
        .post(
          `${process.env.VUE_APP_API_URL}/v1/event/upload-images`,
          formDataImages,
          {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          }
        )
        .then(result => {
          this.form.images = result.data.files;
        })
        .catch(error => {
          console.log(error);
        });
    },
    async uploadTitleImage() {
      let formDataTitleImage = new FormData();
      formDataTitleImage.append("file", this.title_picture);

      await axios
        .post(
          `${process.env.VUE_APP_API_URL}/v1/event/upload-title`,
          formDataTitleImage,
          {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          }
        )
        .then(result => {
          this.form.title_picture = result.data[0];
        })
        .catch(error => {
          console.log(error);
        });
    },
    async createEvent() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        if (this.title_picture) {
          await this.uploadTitleImage();
        }
        if (Array.isArray(this.images) && this.images.length) {
          await this.uploadImages();
        }

        let form = qs.stringify({
          title: this.form.title,
          body: this.form.body,
          place_id: this.form.place.id,
          category_ids: this.form.categories.map(item => item.id),
          members_ids: this.form.members.map(item => item.id),
          startup_id: this.form.startup.id,
          begin_time: this.form.begin_time / 1000,
          title_picture: this.form.title_picture,
          images: this.form.images
        });

        await axios
          .post(`${process.env.VUE_APP_API_URL}/v1/event/create`, form)
          .then(() => {
            this.$toasted.global.success(
              "Событие создано и отправлено на проверку"
            );
            (this.form = {
              title: "",
              body: "",
              categories: [],
              place: {},
              startup: {},
              title_picture: {},
              images: [],
              begin_time: ""
            }),
              this.$refs.pond.removeFiles();
            this.$v.$reset();
            this.$router.push("/playbill");
          })
          .catch(error => {
            error.data.forEach(item => {
              this.$toasted.global.error(item.message);
            });
          });
      }
    },
    notBeforeToday(date) {
      return date < new Date(new Date().setHours(0, 0, 0, 0));
    }
  },
  computed: {
    ...mapGetters("category", ["CATEGORY"]),
    ...mapGetters("place", ["PLACE"]),
    ...mapGetters("startup", ["STARTUP"])
  }
};
</script>

<style scoped></style>
